import React, { Component } from "react";
import styled from "styled-components";
import DetailTemp from "@demoComponents/DetailTemp";
import ApiTable from "@demoComponents/ApiTable";
import CodeDemo from "@demoComponents/CodeDemo";
import Demo1 from "./Demo1";
import Demo2 from "./Demo2";
import Demo3 from "./Demo3";
import Demo4 from "./Demo4";

const Description = styled.section``;
const HashLink = styled.a``;
const Column = styled.div``;

const Apis = styled.section`
  padding-top: 20px;
  width: 100%;
  clear: both;
  table {
    width: 100%;
  }
  td {
    padding: 4px 6px;
  }
`;

class Tooltip extends Component {
  render() {
    const demoList = [
      {
        display: <Demo1 />,
        description: "基本用法",
        code: `import React, { Component } from "react";
import { Tooltip } from "@es";

class Demo1 extends Component {
    render() {
    return (
        <Tooltip title="aosiaois">Tooltip will show on mouse enter.</Tooltip>
    );
    }
}
        `
      },
      {
        display: <Demo2 />,
        description: "位置有12个方向，修改placement",
        code: `import React, { Component } from "react";
import { Tooltip } from "@es";

const grid = {
    TR: "topRight",
    Top: "top",
    TL: "topLeft",
    BR: "bottomRight",
    Bottom: "bottom",
    BL: "bottomLeft",
    LT: "leftTop",
    Left: "left",
    LB: "leftBottom",
    RT: "rightTop",
    Right: "right",
    RB: "rightBottom"
};

class Demo2 extends Component {
    render() {
    return (
        <Grid>
            {Object.keys(grid).map((s, i) => {
                return (
                    <Tooltip key={i} title={"hello world"} placement={grid[s]}>
                        <Cell area={s}>{s}</Cell>
                    </Tooltip>
                );
            })}
        </Grid>
    );
    }
}
        `
      },
      {
        display: <Demo4 />,
        description: "SimpleTooltip, 由css来定位性能更加，会在触发元素外包一层span",
        code: `import React, { Component } from "react";
import { SimpleTooltip } from "@es";
import styled from "styled-components";

const Cell = styled.div``;
const Grid = styled.div\`
  display: grid;
  grid-gap: 5px;
  grid-template-rows: repeat(5, 1fr);
  grid-template-columns: repeat(5, 80px);
  grid-template-areas:
    ". TL Top TR ."
    "LT . . . RT"
    "Left . . . Right"
    "LB . . . RB"
    ". BL Bottom BR .";
  ${Cell} {
    width: 100%;
    text-align: center;
    padding: 0;
    align-self: center;
    justify-self: center;
    background-color: #ddd;
    padding: 5px;
    border-radius: 5px;
    box-sizing: border-box;
    font-family: sans-serif;
    cursor: pointer;
  }
\`;

const grid = {
  TR: "topRight",
  Top: "top",
  TL: "topLeft",
  BR: "bottomRight",
  Bottom: "bottom",
  BL: "bottomLeft",
  LT: "leftTop",
  Left: "left",
  LB: "leftBottom",
  RT: "rightTop",
  Right: "right",
  RB: "rightBottom"
};

class Demo4 extends Component {
  render() {
    return (
      <Grid>
        {Object.keys(grid).map((s, i) => {
          return (
            <SimpleTooltip
              key={i}
              title={"hello world"}
              placement={grid[s]}
              defaultStyles={\`grid-area: ${s};\`}
            >
              <Cell>{s}</Cell>
            </SimpleTooltip>
          );
        })}
      </Grid>
    );
  }
}
`
      },
      {
        display: <Demo3 />,
        description: "设置了 arrowPointAtCenter 后，箭头将指向目标元素的中心。",
        code: `import React, { Component } from "react";
import { Tooltip } from "@es";

class Demo1 extends Component {
  render() {
    return (
      <div>
        <Tooltip placement="topLeft" title="Prompt Text">
          <span>Align edge / 边缘对齐</span>
        </Tooltip>
        <br />
        <Tooltip placement="bottomLeft" title="Prompt Text" arrowPointAtCenter>
          <span>Arrow points to center / 箭头指向中心</span>
        </Tooltip>
      </div>
    );
  }
}`
      }
    ];

    return (
      <DetailTemp title={"Steps"}>
        <Description>
          <h2>描述</h2>
          <p>
            1. 徽标数组件
            <br />
            2. 详细内容查阅<HashLink href="#api">api</HashLink>
          </p>
        </Description>
        <Column className={"demo-column"}>
          {demoList
            //   .filter((ele, index) => index % 2 === 0)
            .map((ele, index) => (
              <CodeDemo display={ele.display} description={ele.description} key={index}>
                {ele.code}
              </CodeDemo>
            ))}
        </Column>
        <Apis>
          <h2>Steps</h2>
          <ApiTable
            data={[
              {
                name: "className",
                description: "顶层样式 class",
                type: "string",
                default: ""
              },
              {
                name: "defaultStyles",
                description: "顶层默认样式",
                type: "string",
                default: ""
              },
              {
                name: "children",
                description: "内容",
                type: "node",
                default: ""
              },
              {
                name: "count",
                description:
                  "展示的数字，大于 overflowCount 时显示为${overflowCount}+，为 0 时隐藏",
                type: "number",
                default: ""
              },
              {
                name: "children",
                description: "内容",
                type: "node",
                default: ""
              },
              {
                name: "children",
                description: "内容",
                type: "node",
                default: ""
              }
            ]}
          />
        </Apis>
      </DetailTemp>
    );
  }
}

export default Tooltip;
